import requestUtil from '@/utils/request'

// 售后服务-获取商品详情
export const getRefundGoodsDetailApi = (orderGoodsId) => {
  return requestUtil({
    url: '/refund/goods',
    params: {
      orderGoodsId
    }
  })
}

// 售后服务提交
export const onSubmitRefundApi = (orderGoodsId, form) => {
  return requestUtil({
    method: 'post',
    url: '/refund/apply',
    data: {
      orderGoodsId,
      form
    }
  })
}

// user-查询我的售后服务list
export const getRefundListApi = (state, page) => {
  return requestUtil.get('/refund/list', { state, page })
}

// 售后订单详情
export const onTargetDetailApi = (orderRefundId) => {
  return requestUtil.get('/refund/detail', orderRefundId)
}
