<template>
  <div class="page-body">
    <van-nav-bar
      fixed
      title="售后服务"
      left-arrow
      @click-left="$router.back()"
    ></van-nav-bar>
    <van-tabs v-model="activeName" @click="changeTab" sticky>
      <van-tab title="全部" name="-1"></van-tab>
      <van-tab title="待处理" name="0"></van-tab>
    </van-tabs>

    <!-- 退款/售后单 -->
    <div>
      <div
        class="widget-detail"
        v-for="(item, index) in refundList"
        :key="index"
      >
        <div class="row-block dis-flex flex-y-center">
          <div class="">{{ item.create_time }}</div>
          <div class="t-r">
            <span class="red">{{ item.state_text }}</span>
          </div>
        </div>
        <div
          class="detail-goods row-block dis-flex"
          @click="onTargetDetail(item.order_refund_id)"
        >
          <div class="goods-image">
            <img class="image" :src="item.orderGoods.goods_image" />
          </div>
          <div class="goods-right">
            <div class="goods-name">
              <span>{{ item.orderGoods.goods_name }}</span>
            </div>
            <div class="goods-num t-r">
              <span>×{{ item.orderGoods.total_num }}</span>
            </div>
          </div>
        </div>
        <div class="detail-order row-block">
          <div class="item dis-flex flex-x-end flex-y-center">
            <span class="">付款金额：</span>
            <span class="red">￥{{ item.orderGoods.total_pay_price }}</span>
          </div>
        </div>
        <div class="detail-operate dis-flex flex-x-end flex-y-center">
          <div
            class="detail-btn btn-detail"
            @click="onTargetDetail(item.order_refund_id)"
          >
            查看详情
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { getRefundListApi } from '@/api/refund'

export default {
  name: 'RefundList',

  data () {
    return {
      activeName: -1,
      state: -1,
      page: 1,

      refundList: []

    }
  },

  methods: {
    async getRefundList (state, page) {
      const { data: { list } } = await getRefundListApi(state, page)
      this.refundList = list.data
    },

    async changeTab (name) {
      await this.getRefundList(name, this.page)
    },

    async onTargetDetail (orderRefundId) {
      this.$toast('敬请期待')
    //   const res = await onTargetDetailApi(orderRefundId)
    }
  },

  created () {
    this.getRefundList(this.state, this.page)
  }
}

</script>

<style lang="less" scoped>
.van-tabs {
  position: sticky;
  top: 46px;
  z-index: 999;
}
.page-body {
  padding: 48px 21px;
}
.dis-flex {
  display: flex;
}
.flex-x-end {
  justify-content: end;
}
.flex-y-center {
  align-content: center;
}
.t-r {
  text-align: right;
}
.goods-num {
  color: #888;
}
.red {
  color: #fa2209;
}
.widget-detail {
  box-sizing: border-box;
  background: #fff;
  margin-bottom: 10px;

  .row-block {
    justify-content: space-between;
    padding: 0 5.1px;
    min-height: 35px;
  }

  .detail-goods {
    padding: 10px;
    background: #f9f9f9;

    .goods-image {
      margin-right: 10px;

      .image {
        display: block;
        width: 100px;
        height: 100px;
      }
    }

    .goods-right {
      padding: 7.8px 0;
    }

    .goods-name {
      margin-bottom: 5px;
    }
  }

  .detail-operate {
    padding-bottom: 10px;

    .detail-btn {
      border-radius: 2px;
      border: 1px solid #ccc;
      padding: 4px 10px;
      font-size: 14px;
      color: #555;
      margin-left: 5px;
    }
  }

  .detail-order {
    padding: 5px 10px;
    font-size: 13px;
    line-height: 25px;
    height: 25px;

    .item {
      margin-bottom: 5px;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }
}
</style>
